.wrapper {
  margin: 0 15px;
  padding: 0 30px;
}

.letterSelection {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px 0 20px 0;
}

.letterGrid {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
}

.playableLettersWrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin:30px 0;
}
